:root {
    --app-bg-color: #ffffff;
    --button-next-bg-color: #b61ecd;
    --answer-bg-color: #e0d1ba;
    --answer-active-bg-color: #f5f5f5;
    --answer-border-color: #efefef;
    --answer-wrong-color: #e74128;
    --answer-correct-color: #52b358;
    --main-text: #1d1d20;
    --gray-text: #9c9c9c;
    --disabled-color: #c6c6c6;
}

* {
    box-sizing: border-box;
}

a, button {
    -webkit-tap-highlight-color: transparent;
}

body {
    min-width: 320px;
    font-size: 16px;
    color: var(--main-text);
    font-family: 'PT Serif', serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
