.wrapper {
    font-family: 'Roboto', sans-serif;
}

.card {
    margin: 0 20px;
}

.number {
    font-weight: 700;
    font-size: 1.4rem;
    color: var(--gray-text);
}

.title {
    font-size: 1rem;
    font-family: 'PT Serif', serif;
    line-height: 1.5;
}

@media (min-width: 375px) {
    .title {
        font-size: 1.2rem;
    }
}
