.wrapper {
    padding: 0 20px;
}

.text {
    margin: 0 0 20px;
    font-size: 20px;
    text-align: center;
    color: black;
}

.button {
    margin-left: auto;
    margin-right: auto;
    padding: 1rem;
    display: block;
    color: #ffffff;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    letter-spacing: 2px;
    font-weight: 700;
    text-transform: uppercase;
    border: 1px solid var(--button-next-bg-color);
    border-radius: 0.5rem;
    background-color: var(--button-next-bg-color);
    transition: all .25s ease;
    cursor: pointer;

    &:hover {
        opacity: 0.85;
    }

    &:active {
        box-shadow: 0 0 0 2px var(--button-next-bg-color);
    }
}
