.answer {
    background: none;
    box-shadow: none;
    border-width: 1px;
    border-color:var(--answer-border-color);
    border-style: solid none;
    text-align: left;
    padding: 24px 20px;
    transition: background-color 0.1s;
    color: #9c9c9c;

    &.nonAnswered {
        color: var(--main-text);
        cursor: pointer;

        &:hover {
            background: #eeeeee;
        }
    }

    & + .answer {
        border-top-style: none;
    }

    &.answered {
        color: var(--main-text);
        cursor: pointer;

        .pct {
            font-weight: 700;
        }
    }

    &.correct {
        box-shadow: inset 3px 0 0 -1px var(--answer-correct-color);

        .title {
            color: var(--answer-correct-color);
        }
    }

    &.notCorrect {
        box-shadow: inset 3px 0 0 -1px var(--answer-wrong-color);

        .title {
            color: var(--answer-wrong-color);
        }
    }

    .comment {
        font-size: 14px;
        margin-top: 16px;
    }
}
